/**
 * 
 * @param {string} username - 
 * @returns {Object}  {valid: boolean, message: string}
 */
export const validateUsername = (username) => {
  if (!username) {
    return { valid: false, message: '' };
  }
  
  if (username.length < 3) {
    return { valid: false, message: '3' };
  }
  
  if (username.length > 20) {
    return { valid: false, message: '20' };
  }
  
  // 
  const usernameRegex = /^[a-zA-Z0-9_-]+$/;
  if (!usernameRegex.test(username)) {
    return { valid: false, message: '' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 
 * @param {string} password - 
 * @returns {Object}  {valid: boolean, message: string, strength: string}
 */
export const validatePassword = (password) => {
  if (!password) {
    return { valid: false, message: '', strength: 'weak' };
  }
  
  if (password.length < 8) {
    return { valid: false, message: '8', strength: 'weak' };
  }
  
  if (password.length > 128) {
    return { valid: false, message: '128', strength: 'weak' };
  }
  
  // 
  let strength = 'weak';
  let score = 0;
  
  // 
  if (/[a-z]/.test(password)) score++;
  // 
  if (/[A-Z]/.test(password)) score++;
  // 
  if (/\d/.test(password)) score++;
  // 
  if (/[!@#$%^&*(),.?":{}|<>]/.test(password)) score++;
  // 12
  if (password.length >= 12) score++;
  
  if (score >= 4) {
    strength = 'strong';
  } else if (score >= 3) {
    strength = 'medium';
  }
  
  if (score < 3) {
    return { 
      valid: false, 
      message: '', 
      strength 
    };
  }
  
  return { valid: true, message: '', strength };
};

/**
 * 
 * @param {string} email - 
 * @returns {Object}  {valid: boolean, message: string}
 */
export const validateEmail = (email) => {
  if (!email) {
    return { valid: false, message: '' };
  }
  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (!emailRegex.test(email)) {
    return { valid: false, message: '' };
  }
  
  if (email.length > 254) {
    return { valid: false, message: '' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 
 * @param {string} password - 
 * @param {string} confirmPassword - 
 * @returns {Object}  {valid: boolean, message: string}
 */
export const validateConfirmPassword = (password, confirmPassword) => {
  if (!confirmPassword) {
    return { valid: false, message: '' };
  }
  
  if (password !== confirmPassword) {
    return { valid: false, message: '' };
  }
  
  return { valid: true, message: '' };
};

/**
 * MFA
 * @param {string} code - 6
 * @returns {Object}  {valid: boolean, message: string}
 */
export const validateMfaCode = (code) => {
  if (!code) {
    return { valid: false, message: '' };
  }
  
  // 6
  const codeRegex = /^\d{6}$/;
  if (!codeRegex.test(code)) {
    return { valid: false, message: '6' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 
 * @param {string} filename - 
 * @returns {Object}  {valid: boolean, message: string}
 */
export const validateFilename = (filename) => {
  if (!filename) {
    return { valid: false, message: '' };
  }
  
  if (filename.length > 255) {
    return { valid: false, message: '' };
  }
  
  // 
  const forbiddenChars = /[<>:"/\\|?*]/;
  if (forbiddenChars.test(filename)) {
    return { valid: false, message: '' };
  }
  
  // 
  const forbiddenNames = ['CON', 'PRN', 'AUX', 'NUL', 'COM1', 'COM2', 'COM3', 'COM4', 'COM5', 'COM6', 'COM7', 'COM8', 'COM9', 'LPT1', 'LPT2', 'LPT3', 'LPT4', 'LPT5', 'LPT6', 'LPT7', 'LPT8', 'LPT9'];
  if (forbiddenNames.includes(filename.toUpperCase())) {
    return { valid: false, message: '' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 
 * @param {Object} formData - 
 * @param {Object} rules - 
 * @returns {Object}  {valid: boolean, errors: Object}
 */
export const validateForm = (formData, rules) => {
  const errors = {};
  let valid = true;
  
  for (const field in rules) {
    const value = formData[field];
    const fieldRules = rules[field];
    
    for (const rule of fieldRules) {
      const result = rule.validator(value);
      if (!result.valid) {
        errors[field] = result.message;
        valid = false;
        break; // 
      }
    }
  }
  
  return { valid, errors };
};

/**
 * 
 */
export const validationRules = {
  required: (message = '') => ({
    validator: (value) => {
      if (value === null || value === undefined || value === '') {
        return { valid: false, message };
      }
      return { valid: true, message: '' };
    }
  }),
  
  minLength: (min, message) => ({
    validator: (value) => {
      if (value && value.length < min) {
        return { valid: false, message: message || `${min}` };
      }
      return { valid: true, message: '' };
    }
  }),
  
  maxLength: (max, message) => ({
    validator: (value) => {
      if (value && value.length > max) {
        return { valid: false, message: message || `${max}` };
      }
      return { valid: true, message: '' };
    }
  }),
  
  pattern: (regex, message) => ({
    validator: (value) => {
      if (value && !regex.test(value)) {
        return { valid: false, message };
      }
      return { valid: true, message: '' };
    }
  })
};
