<template>
  <Teleport to="body">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content wide" @click.stop>
        <div class="modal-header">
          <h3>{{ isEnabled ? '' : '' }}</h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
      
      <div class="modal-body">
        <!-- MFA -->
        <div v-if="isEnabled" class="mfa-enabled-view">
          <div class="status-info">
            <div class="status-icon enabled">
              <i class="fas fa-shield-alt"></i>
            </div>
            <div class="status-text">
              <h4></h4>
              <p></p>
            </div>
          </div>
          
          <div class="action-buttons">
            <button class="modal-btn secondary" @click="showDisableForm = true">
              <i class="fas fa-times-circle"></i>
              
            </button>
          </div>
          
          <!--  -->
          <div v-if="showDisableForm" class="disable-confirm">
            <p class="warning-text">
              <i class="fas fa-exclamation-triangle"></i>
              
            </p>
            <div class="form-actions">
              <button class="modal-btn secondary" @click="showDisableForm = false"></button>
              <button 
                class="modal-btn primary" 
                :disabled="loading"
                @click="handleDisableMfa"
              >
                <i v-if="loading" class="fas fa-spinner fa-spin"></i>
                
              </button>
            </div>
          </div>
        </div>
        
        <!-- MFA -->
        <div v-else class="mfa-setup-view">
          <div v-if="!setupData" class="setup-intro">
            <div class="intro-content">
              <div class="intro-icon">
                <i class="fas fa-shield-alt"></i>
              </div>
              <h4></h4>
              <p>6</p>
              <div class="app-recommendations">
                <p><strong></strong></p>
                <ul>
                  <li>Google Authenticator</li>
                  <li>Microsoft Authenticator</li>
                  <li>Authy</li>
                </ul>
              </div>
            </div>
            <button class="modal-btn primary" @click="startSetup" :disabled="loading">
              <i v-if="loading" class="fas fa-spinner fa-spin"></i>
              {{ loading ? '...' : '' }}
            </button>
          </div>
          
          <!--  -->
          <div v-if="setupData && !setupComplete" class="setup-steps">
            <div class="step">
              <div class="step-number">1</div>
              <div class="step-content">
                <h5></h5>
                <p></p>
                <div class="qr-container">
                  <div class="qr-code">
                    <QrcodeVue 
                      v-if="setupData?.qr_url"
                      :value="setupData.qr_url" 
                      :size="200"
                      level="M"
                      render-as="canvas"
                      background="#ffffff"
                      foreground="#000000"
                    />
                  </div>
                </div>
                <div class="manual-setup">
                  <p></p>
                  <div class="secret-display">
                    <code>{{ setupData.secret }}</code>
                    <button class="copy-btn" @click="copySecret">
                      <i class="fas fa-copy"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
            
            <div class="step">
              <div class="step-number">2</div>
              <div class="step-content">
                <h5></h5>
                <p>6</p>
                <div class="form-group">
                  <input
                    v-model="verificationCode"
                    type="text"
                    class="verification-input"
                    placeholder="000000"
                    maxlength="6"
                    @input="handleCodeInput"
                  />
                  <div v-if="verificationError" class="error-message">
                    {{ verificationError }}
                  </div>
                </div>
                <button 
                  class="modal-btn primary" 
                  :disabled="verificationCode.length !== 6 || loading"
                  @click="verifySetup"
                >
                  <i v-if="loading" class="fas fa-spinner fa-spin"></i>
                  {{ loading ? '...' : '' }}
                </button>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div v-if="setupComplete" class="setup-complete">
            <div class="success-icon">
              <i class="fas fa-check-circle"></i>
            </div>
            <h4></h4>
            <p></p>
          </div>
        </div>
      </div>
      
      <div class="modal-footer" v-if="!showDisableForm && !setupData">
        <button class="modal-btn secondary" @click="closeDialog">
          {{ setupComplete ? '' : '' }}
        </button>
      </div>
    </div>
  </div>
  </Teleport>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';
import QrcodeVue from 'qrcode-vue3';

// Props 
defineProps({
  isEnabled: {
    type: Boolean,
    default: false
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 
const loading = ref(false);
const setupData = ref(null);
const setupComplete = ref(false);
const verificationCode = ref('');
const verificationError = ref('');
const showDisableForm = ref(false);

// MFA
const startSetup = async () => {
  loading.value = true;
  try {
    // MFA
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: new URLSearchParams({ action: 'generate_mfa_secret' })
    });
    
    if (result.status === 'ok' && result.mfa_secret) {
      setupData.value = {
        secret: result.mfa_secret,
        qr_url: `otpauth://totp/${encodeURIComponent('')}:${encodeURIComponent(result.username)}?secret=${result.mfa_secret}&issuer=${encodeURIComponent('')}`
      };
    } else {
      ElMessage.error(result.message || 'MFA');
    }
  } catch (error) {
    console.error('MFA:', error);
    ElMessage.error('MFA');
  } finally {
    loading.value = false;
  }
};

// 
const copySecret = async () => {
  try {
    await navigator.clipboard.writeText(setupData.value.secret);
    ElMessage.success('');
  } catch (error) {
    console.error(':', error);
    ElMessage.error('');
  }
};

// 
const handleCodeInput = () => {
  verificationError.value = '';
  // 
  verificationCode.value = verificationCode.value.replace(/\D/g, '');
};

// MFA
const verifySetup = async () => {
  if (verificationCode.value.length !== 6) {
    verificationError.value = '6';
    return;
  }
  
  loading.value = true;
  try {
    // MFA
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: new URLSearchParams({ 
        action: 'verify_and_enable_mfa', 
        secret: setupData.value.secret,
        code: verificationCode.value 
      })
    });
    
    if (result.status === 'ok') {
      setupComplete.value = true;
      ElMessage.success('');
      setTimeout(() => {
        emit('success');
        closeDialog();
      }, 2000);
    } else {
      verificationError.value = result.message || '';
    }
  } catch (error) {
    console.error('MFA:', error);
    verificationError.value = 'MFA';
  } finally {
    loading.value = false;
  }
};

// MFA
const handleDisableMfa = async () => {
  loading.value = true;
  try {
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: new URLSearchParams({ action: 'disable_mfa' })
    });
    
    if (result.status === 'ok') {
      ElMessage.success('');
      emit('success');
      closeDialog();
    } else {
      ElMessage.error(result.message || '');
    }
  } catch (error) {
    console.error('MFA:', error);
    ElMessage.error('MFA');
  } finally {
    loading.value = false;
  }
};

// 
const closeDialog = () => {
  emit('close');
};
</script>

