<template>
  <div class="file-list-container">
    <div class="file-card">
      <!--  -->
      <div class="search-and-batch-actions">
        <div class="left-section">
          <h3></h3>
          <!--  -->
          <div v-if="statistics" class="stats-row">
            <div class="stat-item">
              <i class="fas fa-users"></i>
              <span>: {{ statistics.total_users }}</span>
            </div>
            <div class="stat-item">
              <i class="fas fa-shield-alt"></i>
              <span>MFA: {{ statistics.mfa_enabled_users }}</span>
            </div>
            <div class="stat-item">
              <i class="fas fa-envelope"></i>
              <span>: {{ statistics.users_with_email }}</span>
            </div>
          </div>
        </div>
        <div class="right-section">
          <div class="action-buttons">
            <button class="batch-btn" @click="openCreateUserDialog">
              <i class="fas fa-user-plus"></i> 
            </button>
            <button class="batch-btn" @click="loadUsers">
              <i class="fas fa-sync-alt"></i> 
            </button>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="loading" class="loading-cell">
        <i class="fas fa-circle-notch fa-spin"></i>
        <span>...</span>
      </div>

      <!--  -->
      <div v-else-if="users.length > 0" class="table-wrapper">
        <table class="file-table table-min">
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th>MFA</th>
              <th>IP</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="user in users" :key="user.id">
              <td>{{ user.id }}</td>
              <td>
                <div class="file-name-cell">
                  <i class="fas fa-user file-icon"></i>
                  <span>{{ user.username }}</span>
                </div>
              </td>
              <td>
                <span v-if="user.email" class="email">{{ user.email }}</span>
                <span v-else class="no-email"></span>
              </td>
              <td>
                <span :class="['mfa-badge', user.mfa_enabled ? 'enabled' : 'disabled']">
                  <i :class="user.mfa_enabled ? 'fas fa-shield-alt' : 'fas fa-shield'"></i>
                  {{ user.mfa_enabled ? '' : '' }}
                </span>
              </td>
              <td>
                <span v-if="user.ip" class="ip-address">{{ user.ip }}</span>
                <span v-else class="no-data">-</span>
              </td>
              <td>
                <span v-if="user.last_login" class="login-time">
                  {{ formatTime(user.last_login) }}
                </span>
                <span v-else class="no-data"></span>
              </td>
              <td>
                <div class="action-buttons">
                  <button 
                    class="action-btn" 
                    @click="openEditUserDialog(user)"
                    title=""
                  >
                    <i class="fas fa-key"></i>
                  </button>
                  <button 
                    v-if="user.mfa_enabled"
                    class="action-btn" 
                    @click="disableUserMfa(user)"
                    title="MFA"
                  >
                    <i class="fas fa-shield"></i>
                  </button>
                  <button 
                    class="action-btn delete-btn" 
                    @click="deleteUser(user)"
                    title=""
                  >
                    <i class="fas fa-trash"></i>
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!--  -->
      <div v-else class="empty-cell">
        <i class="fas fa-users"></i>
        <h4></h4>
        <p>""</p>
      </div>

      <!--  -->
      <div v-if="pagination.total > 0" class="pagination-container">
        <div class="page-info">
           {{ (pagination.page - 1) * pagination.limit + 1 }} - 
          {{ Math.min(pagination.page * pagination.limit, pagination.total) }} 
           {{ pagination.total }} 
        </div>
        <div class="pagination">
          <button 
            :disabled="pagination.page <= 1"
            @click="changePage(pagination.page - 1)"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
          <span class="page-info">
            {{ pagination.page }} / {{ pagination.total_pages }}
          </span>
          <button 
            :disabled="pagination.page >= pagination.total_pages"
            @click="changePage(pagination.page + 1)"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>

    <!-- / -->
    <UserFormDialog
      :visible="userFormDialog.visible"
      :user="userFormDialog.user"
      :is-edit="userFormDialog.isEdit"
      @close="closeUserFormDialog"
      @success="handleUserFormSuccess"
    />
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, inject } from 'vue';
import { ElMessage } from 'element-plus';
import { apiRequest } from '@/utils/auth.js';
import UserFormDialog from './UserFormDialog.vue';

// 
const loading = ref(false);
const users = ref([]);
const statistics = ref(null);
const pagination = reactive({
  page: 1,
  limit: 20,
  total: 0,
  total_pages: 0
});

// 
const userFormDialog = reactive({
  visible: false,
  user: null,
  isEdit: false
});

// 
const confirm = inject('confirm');

// 
const loadUsers = async () => {
  loading.value = true;
  try {
    const result = await apiRequest('/api/auth/user-management-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
        action: 'list_users',
        page: pagination.page,
        limit: pagination.limit
      })
    });
    
    if (result.status === 'ok') {
      users.value = result.data || [];
      Object.assign(pagination, result.pagination);
    } else {
      ElMessage.error(result.message || '');
    }
  } catch (error) {
    console.error(':', error);
    ElMessage.error('');
  } finally {
    loading.value = false;
  }
};

// 
const loadStatistics = async () => {
  try {
    const result = await apiRequest('/api/auth/user-management-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
        action: 'get_statistics'
      })
    });
    
    if (result.status === 'ok') {
      statistics.value = result.data;
    }
  } catch (error) {
    console.error(':', error);
  }
};

// 
const changePage = (page) => {
  pagination.page = page;
  loadUsers();
};

// 
const formatTime = (timeStr) => {
  if (!timeStr) return '-';
  try {
    const date = new Date(timeStr);
    return date.toLocaleString('zh-CN');
  } catch (error) {
    return timeStr;
  }
};

// 
const openCreateUserDialog = () => {
  userFormDialog.visible = true;
  userFormDialog.user = null;
  userFormDialog.isEdit = false;
};

// 
const openEditUserDialog = (user) => {
  userFormDialog.visible = true;
  userFormDialog.user = { ...user };
  userFormDialog.isEdit = true;
};

// 
const closeUserFormDialog = () => {
  userFormDialog.visible = false;
  userFormDialog.user = null;
  userFormDialog.isEdit = false;
};

// 
const handleUserFormSuccess = () => {
  closeUserFormDialog();
  loadUsers();
  loadStatistics();
};

// MFA
const disableUserMfa = async (user) => {
  try {
    const confirmed = await confirm.open({
      title: '',
      message: ` "${user.username}" MFA`,
      confirmButtonText: '',
      cancelButtonText: ''
    });

    if (!confirmed) return;

    const result = await apiRequest('/api/auth/user-management-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
        action: 'disable_mfa',
        user_id: user.id
      })
    });
    
    if (result.status === 'ok') {
      ElMessage.success(result.message || 'MFA');
      loadUsers();
      loadStatistics();
    } else {
      ElMessage.error(result.message || 'MFA');
    }
  } catch (error) {
    console.error('MFA:', error);
    ElMessage.error('');
  }
};

// 
const deleteUser = async (user) => {
  try {
    const confirmed = await confirm.open({
      title: '',
      message: ` "${user.username}" `,
      confirmButtonText: '',
      cancelButtonText: ''
    });

    if (!confirmed) return;

    const result = await apiRequest('/api/auth/user-management-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: new URLSearchParams({
        action: 'delete_user',
        user_id: user.id
      })
    });

    if (result.status === 'ok') {
      ElMessage.success(result.message || '');
      loadUsers();
      loadStatistics();
    } else {
      ElMessage.error(result.message || '');
    }
  } catch (error) {
    console.error(':', error);
    ElMessage.error('');
  }
};

// 
onMounted(() => {
  loadUsers();
  loadStatistics();
});
</script>

