<template>
  <Teleport to="body">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3 class="modal-title"></h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="modal-body">
          <div class="current-info" v-if="currentEmail">
            <label class="info-label"></label>
            <div class="info-value">{{ currentEmail }}</div>
          </div>
          <div class="form-group">
            <label class="form-label"></label>
            <input
              v-model="formData.newEmail"
              type="email"
              class="form-input"
              placeholder=""
              @blur="validateField('newEmail')"
              @input="clearError('newEmail')"
            />
            <div v-if="errors.newEmail" class="error-message">
              {{ errors.newEmail }}
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button class="modal-btn secondary" @click="closeDialog"></button>
          <button 
            class="modal-btn primary" 
            :disabled="!isFormValid || loading"
            @click="handleSubmit"
          >
            <span v-if="loading" class="loading-spinner"></span>
            {{ loading ? '...' : '' }}
          </button>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { request } from '@/utils/auth.js';
// updateEmailfetch

// Props
const props = defineProps({
  currentEmail: {
    type: String,
    default: ''
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 
const loading = ref(false);
const formData = reactive({
  newEmail: ''
});

const errors = reactive({
  newEmail: ''
});

// 
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'newEmail':
      if (!value.trim()) {
        errors[field] = '';
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
        errors[field] = '';
      } else if (value === props.currentEmail) {
        errors[field] = '';
      } else {
        errors[field] = '';
      }
      break;
  }
};

// 
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 
const isFormValid = computed(() => {
  const hasValues = formData.newEmail;
  const noErrors = !Object.values(errors).some(error => error !== '');
  return hasValues && noErrors;
});

// 
const handleSubmit = async () => {
  // 
  Object.keys(formData).forEach(field => {
    validateField(field);
  });
  if (!isFormValid.value) {
    ElMessage.error('');
    return;
  }
  loading.value = true;
  try {
    const result = await request('/api/auth/account-settings-api.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: new URLSearchParams({
        action: 'update_email',
        new_email: formData.newEmail
      })
    });
    
    if (result.status === 'ok') {
      ElMessage.success(result.message || '');
      emit('success');
      closeDialog();
    } else {
      ElMessage.error(result.message || '');
    }
  } catch (error) {
    console.error(':', error);
    ElMessage.error('');
  } finally {
    loading.value = false;
  }
};

// 
const closeDialog = () => {
  emit('close');
};
</script>

