.current-info {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 16px;
}

.info-label {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
  color: #e8e8e8;
  font-size: 14px;
}

.info-value {
  color: #b0b0b0;
  font-size: 14px;
  font-family: monospace;
}

.error-message {
  color: #ff6b6b;
  font-size: 12px;
  margin-top: 4px;
}
</style>
