<template>
  <div class="site-settings-container">
    <!--  -->
    <div class="site-settings-card">
        <div class="site-usage-title">
          <i class="fas fa-info-circle"></i>
          
        </div>
        <div class="site-usage-content">
          <p></p>
          <ul>
            <li> <strong>Docker </strong> </li>
            <li> <strong>PHP </strong>  Vue  <strong>Apache 2.0 </strong> </li>
            <li> <strong>StarUI v3 </strong></li>
            <li> <strong>JSON </strong> </li>
            <li></li>
          </ul>
        </div>
        <div class="site-usage-links">
          <a href="https://www.xcdream.com/ykc" target="_blank" class="site-usage-link external">
            <i class="fas fa-globe"></i> 
          </a>
          <a href="https://github.com/defeatedperson/ykc" target="_blank" class="site-usage-link external">
            <i class="fab fa-github"></i> 
          </a>
          <a href="https://re.xcdream.com/9390.html" target="_blank" class="site-usage-link external">
            <i class="fas fa-book"></i> 
          </a>
          <a href="https://re.xcdream.com/links/qiafan" target="_blank" class="site-usage-link external">
            <i class="fas fa-heart"></i> 
          </a>
        </div>
    </div>

    <!--  -->
    <div class="site-settings-card">
      <div class="site-settings-header">
        <h3></h3>
      </div>
      <div v-if="siteLoading" class="loading-cell">
        <i class="fas fa-circle-notch fa-spin"></i>
        <span>...</span>
      </div>
      <div v-else class="site-settings-section">
        <div class="site-config-editor">
          <textarea
            v-model="siteConfigText"
            placeholder="JSON..."
            rows="8"
            class="site-json-editor"
          ></textarea>
          <div class="site-editor-actions">
            <button @click="resetSiteConfig" class="batch-btn delete-btn">
              <i class="fas fa-undo"></i> 
            </button>
            <button @click="loadSiteConfig" class="batch-btn">
              <i class="fas fa-sync"></i> 
            </button>
            <button @click="saveSiteConfig" class="batch-btn" :disabled="saving">
              <i class="fas fa-save"></i> {{ saving ? '...' : '' }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="site-settings-card">
      <div class="site-settings-header">
        <h3></h3>
      </div>
      <div v-if="menuLoading" class="loading-cell">
        <i class="fas fa-circle-notch fa-spin"></i>
        <span>...</span>
      </div>
      <div v-else class="site-settings-section">
        <div class="site-config-editor">
          <textarea
            v-model="menuConfigText"
            placeholder="JSON..."
            rows="8"
            class="site-json-editor"
          ></textarea>
          <div class="site-editor-actions">
            <button @click="resetMenuConfig" class="batch-btn delete-btn">
              <i class="fas fa-undo"></i> 
            </button>
            <button @click="loadMenuConfig" class="batch-btn">
              <i class="fas fa-sync"></i> 
            </button>
            <button @click="saveMenuConfig" class="batch-btn" :disabled="saving">
              <i class="fas fa-save"></i> {{ saving ? '...' : '' }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { apiRequest } from '@/utils/auth'
import { inject } from 'vue'

export default {
  name: 'SiteSettings',
  setup() {
    const notification = inject('notification')
    return { notification }
  },
  data() {
    return {
      siteConfigText: '',
      menuConfigText: '',
      originalSiteConfig: '',
      originalMenuConfig: '',
      saving: false,
      siteLoading: false,
      menuLoading: false
    }
  },
  async mounted() {
    await this.loadSiteConfig()
    await this.loadMenuConfig()
  },
  methods: {
    async loadSiteConfig() {
      this.siteLoading = true
      try {
        const formData = new FormData()
        formData.append('action', 'get_site_config')
        
        const result = await apiRequest('/api/site.php', {
          method: 'POST',
          body: formData
        })
        
        if (result.status === 'ok') {
          const configText = JSON.stringify(result.data, null, 2)
          this.siteConfigText = configText
          this.originalSiteConfig = configText
        } else {
          this.showMessage(result.message || '', 'error')
        }
      } catch (error) {
        this.showMessage(': ' + error.message, 'error')
      } finally {
        this.siteLoading = false
      }
    },

    async saveSiteConfig() {
      if (!this.siteConfigText.trim()) {
        this.showMessage('', 'error')
        return
      }

      try {
        // JSON
        JSON.parse(this.siteConfigText)
      } catch (error) {
        this.showMessage('JSON: ' + error.message, 'error')
        return
      }

      this.saving = true
      try {
        const formData = new FormData()
        formData.append('action', 'update_site_config')
        formData.append('config', this.siteConfigText)
        
        const result = await apiRequest('/api/site.php', {
          method: 'POST',
          body: formData
        })
        
        if (result.status === 'ok') {
          this.originalSiteConfig = this.siteConfigText
          this.showMessage('', 'success')
          // 
          window._siteConfigCache = null
        } else {
          this.showMessage(result.message || '', 'error')
        }
      } catch (error) {
        this.showMessage(': ' + error.message, 'error')
      } finally {
        this.saving = false
      }
    },

    resetSiteConfig() {
      // site.json
      const defaultSite = {
        siteName: "",
        keywords: ", , , , ",
        description: "",
        icp: "ICP12345678",
        securityRecord: " 12345678901234"
      }
      this.siteConfigText = JSON.stringify(defaultSite, null, 2)
      this.showMessage('', 'success')
    },

    async loadMenuConfig() {
      this.menuLoading = true
      try {
        const formData = new FormData()
        formData.append('action', 'get_menu_config')
        
        const result = await apiRequest('/api/site.php', {
          method: 'POST',
          body: formData
        })
        
        if (result.status === 'ok') {
          const configText = JSON.stringify(result.data, null, 2)
          this.menuConfigText = configText
          this.originalMenuConfig = configText
        } else {
          this.showMessage(result.message || '', 'error')
        }
      } catch (error) {
        this.showMessage(': ' + error.message, 'error')
      } finally {
        this.menuLoading = false
      }
    },

    async saveMenuConfig() {
      if (!this.menuConfigText.trim()) {
        this.showMessage('', 'error')
        return
      }

      try {
        // JSON
        JSON.parse(this.menuConfigText)
      } catch (error) {
        this.showMessage('JSON: ' + error.message, 'error')
        return
      }

      this.saving = true
      try {
        const formData = new FormData()
        formData.append('action', 'update_menu_config')
        formData.append('config', this.menuConfigText)
        
        const result = await apiRequest('/api/site.php', {
          method: 'POST',
          body: formData
        })
        
        if (result.status === 'ok') {
          this.originalMenuConfig = this.menuConfigText
          this.showMessage('', 'success')
        } else {
          this.showMessage(result.message || '', 'error')
        }
      } catch (error) {
        this.showMessage(': ' + error.message, 'error')
      } finally {
        this.saving = false
      }
    },

    resetMenuConfig() {
      // menu.json
      const defaultMenu = [
        { name: "", url: "/" },
        { name: "", url: "https://www.xcdream.com/home" },
        { name: "", url: "https://www.xcdream.com/home" },
        { name: "", url: "https://www.xcdream.com/home" }
      ]
      this.menuConfigText = JSON.stringify(defaultMenu, null, 2)
      this.showMessage('', 'success')
    },

    showMessage(text, type = 'success') {
      if (this.notification) {
        this.notification.open({
          title: type === 'success' ? '' : '',
          message: text,
          type: type,
          duration: 3000
        })
      } else {
        // 
        console.log(`[${type.toUpperCase()}] ${text}`)
      }
    }
  }
}
</script>

<style scoped>
@import './site-settings.css';
</style>
