<template>
  <Teleport to="body" v-if="visible">
    <div class="modal-overlay" @click="closeDialog">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3 class="modal-title">{{ isEdit ? '' : '' }}</h3>
          <button class="modal-close" @click="closeDialog">
            <i class="fas fa-times"></i>
          </button>
        </div>
        
        <div class="modal-body">
          
          <!--  -->
          <div v-if="isEdit && user" class="current-info">
            <label class="info-label"></label>
            <div class="info-value">
              <div class="user-info-row">
                <span class="info-item">: {{ user.username }}</span>
                <span class="info-item">ID: {{ user.id }}</span>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="form-group">
            <label class="form-label"> *</label>
            <input
              v-model="formData.username"
              type="text"
              class="form-input"
              :placeholder="isEdit ? '' : ''"
              :disabled="isEdit"
              @blur="validateField('username')"
              @input="clearError('username')"
            />
            <div v-if="errors.username" class="error-message">
              {{ errors.username }}
            </div>
            <div class="form-hint">
              3-30
            </div>
          </div>

          <!--  -->
          <div class="form-group">
            <label class="form-label">
              {{ isEdit ? ' *' : ' *' }}
            </label>
            <div class="password-input-container">
              <input
                v-model="formData.password"
                :type="showPassword ? 'text' : 'password'"
                class="form-input"
                :placeholder="isEdit ? '' : ''"
                @blur="validateField('password')"
                @input="clearError('password')"
              />
              <button
                type="button"
                class="password-toggle"
                @click="showPassword = !showPassword"
              >
                <i :class="showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'"></i>
              </button>
            </div>
            <div v-if="errors.password" class="error-message">
              {{ errors.password }}
            </div>
            <div class="form-hint">
              {{ isEdit ? '' : '8' }}
            </div>
          </div>

          <!--  -->
          <div class="form-group">
            <label class="form-label"></label>
            <input
              v-model="formData.email"
              type="email"
              class="form-input"
              placeholder=""
              @blur="validateField('email')"
              @input="clearError('email')"
            />
            <div v-if="errors.email" class="error-message">
              {{ errors.email }}
            </div>
            <div class="form-hint">
              
            </div>
          </div>
        </div>
        
        <div class="modal-footer">
          <button class="modal-btn secondary" @click="closeDialog"></button>
          <button 
            :class="['modal-btn', isEdit ? 'danger' : 'primary']" 
            :disabled="!isFormValid || loading"
            @click="handleSubmit"
          >
            <i v-if="loading" class="fas fa-spinner fa-spin"></i>
            {{ loading ? (isEdit ? '...' : '...') : (isEdit ? '' : '') }}
          </button>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup>
import { ref, reactive, computed, watch, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { apiRequest } from '@/utils/auth.js';
import { validateUsername, validatePassword, validateEmail } from '@/utils/validator.js';

// Props
const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  user: {
    type: Object,
    default: null
  },
  isEdit: {
    type: Boolean,
    default: false
  }
});

// Emits
const emit = defineEmits(['close', 'success']);

// 
const loading = ref(false);
const showPassword = ref(false);

const formData = reactive({
  username: '',
  password: '',
  email: ''
});

const errors = reactive({
  username: '',
  password: '',
  email: ''
});

// 
const isFormValid = computed(() => {
  if (props.isEdit) {
    // reset_password
    const passwordValidation = validatePassword(formData.password);
    return formData.password.trim() && 
           passwordValidation.valid &&
           !errors.username && 
           !errors.password && 
           !errors.email;
  } else {
    // 
    const usernameValidation = validateUsername(formData.username);
    const passwordValidation = validatePassword(formData.password);
    return formData.username.trim() && 
           formData.password.trim() && 
           usernameValidation.valid &&
           passwordValidation.valid &&
           !errors.username && 
           !errors.password && 
           !errors.email;
  }
});

// /
watch(() => props.visible, (visible) => {
  if (visible) {
    resetForm();
    if (props.isEdit && props.user) {
      // 
      formData.username = props.user.username || '';
      formData.email = props.user.email || '';
      formData.password = ''; // 
    }
  }
});

// 
const resetForm = () => {
  formData.username = '';
  formData.password = '';
  formData.email = '';
  
  errors.username = '';
  errors.password = '';
  errors.email = '';
  
  showPassword.value = false;
};

// 
const validateField = (field) => {
  const value = formData[field];
  
  switch (field) {
    case 'username': {
      if (!props.isEdit) {
        // 
        const validation = validateUsername(value);
        errors[field] = validation.valid ? '' : validation.message;
      } else {
        // 
        errors[field] = '';
      }
      break;
    }
    case 'password': {
      // 
      const validation = validatePassword(value);
      if (!props.isEdit) {
        // 
        if (!value.trim()) {
          errors[field] = '';
        } else {
          errors[field] = validation.valid ? '' : validation.message;
        }
      } else {
        // 
        if (!value.trim()) {
          errors[field] = '';
        } else {
          errors[field] = validation.valid ? '' : validation.message;
        }
      }
      break;
    }
    case 'email': {
      if (value.trim()) {
        // 
        const validation = validateEmail(value);
        errors[field] = validation.valid ? '' : validation.message;
      } else {
        // 
        errors[field] = '';
      }
      break;
    }
  }
};

// 
const clearError = (field) => {
  errors[field] = '';
};

// 
const closeDialog = () => {
  emit('close');
};

// 
const handleSubmit = async () => {
  // 
  validateField('username');
  validateField('password');
  validateField('email');
  
  if (!isFormValid.value) {
    ElMessage.error('');
    return;
  }
  
  loading.value = true;
  
  try {
    if (props.isEdit) {
      // 
      if (!formData.password.trim()) {
        ElMessage.error('');
        loading.value = false;
        return;
      }
      
      const bodyParams = new URLSearchParams();
      bodyParams.append('action', 'reset_password');
      bodyParams.append('user_id', props.user.id);
      bodyParams.append('new_password', formData.password.trim());
      
      console.log(':', Array.from(bodyParams.entries()));
      
      const result = await apiRequest('/api/auth/user-management-api.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: bodyParams
      });
      
      if (result.status === 'ok') {
        ElMessage.success('');
        emit('success');
      } else {
        // 
        emit('close');
        setTimeout(() => {
          ElMessage.error(result.message || '');
        }, 100);
      }
    } else {
      // 
      const bodyParams = new URLSearchParams();
      bodyParams.append('action', 'create_user');
      bodyParams.append('username', formData.username.trim());
      bodyParams.append('password', formData.password.trim());
      bodyParams.append('email', formData.email.trim());
      
      console.log(':', Array.from(bodyParams.entries()));
      
      const result = await apiRequest('/api/auth/user-management-api.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: bodyParams
      });
      
      if (result.status === 'ok') {
        ElMessage.success(result.message || '');
        emit('success');
      } else {
        // 
        emit('close');
        setTimeout(() => {
          ElMessage.error(result.message || '');
        }, 100);
      }
    }
  } catch (error) {
    console.error(':', error);
    // 
    emit('close');
    setTimeout(() => {
      ElMessage.error('');
    }, 100);
  } finally {
    loading.value = false;
  }
};
</script>

